*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

/* Header Component */

#header{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: linear-gradient(#9626ff,#d13aff);
    height: 3.5em;
    color: white;
    text-transform: uppercase;
    font-family: 800;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}
#header p{
    text-transform: capitalize;
    font-weight: 550;
    font-style: italic;
    font-size: 18px;
}
#textArea{
    text-align: center;
    margin-top: 8em ;
    margin-bottom: 2em ;
}
#textArea textarea{
    height: 50vh;
    width: 60vw;
    outline: none;
    border: 2px solid #d13aff;
    font-size: 18px;
    background: transparent;
    color: #9626ff;
    font-weight: 550;
    font-style: italic;
    border-radius: 0.3em;
    padding: 1em;
    box-shadow: #eba9ff 1px 2px 4px 4px;

}
::placeholder{
    color: #d13aff;
}
#textArea .properties{
    color: #d13aff;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 60%;
    margin: auto;
    height: 2em;
    font-size: 22px;
    font-weight: 600;
}
#btn button{
    transition: all 0.25s;
    font-size: 22px;
    padding: 0.4em 0.5em;
    font-weight: 700;
    color: white;
    background: linear-gradient(#9626ff,#d13aff);
    border: none;
    outline: none;
    border-radius: 3px;
    cursor: pointer;
}
.buttonGroup{
    display: flex;
    gap: 5px;
    justify-content: center;
    width: 70%;
    flex-wrap: wrap;
    margin: auto;
}
#btn button:hover{
    background: linear-gradient(#d447ff,#911cff);
}
::selection{
    color: white;
    background: #911cff;
}
#footer{
    height: 28vh;
    background: linear-gradient(#9626ff,#d13aff);
    margin-top: 5em;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
#footer h2{
    font-size: 42px;
    margin-top: 5px;
}
#footer pre{
    font-size: 24px;
    font-weight: 600;
}
#footer p{
    font-weight: 500;
    font-family: cursive;
}
#footer .icons{
    display: flex;
    width: 40%;
    justify-content: space-around;
    align-items: center;
    margin-top: 2em;
}
#footer .icons i{
    cursor: pointer;
}

@media (max-width : 900px) {
    #header{
        height: 3.2em;
    }
    #header h1{
        font-size: 28px;
    }
    #header p{
        font-size: 16px;
        font-weight: 550;
    }
    #textArea textarea{
        height: 55vh;
        width: 75vw;
        font-size: 16px;
        font-weight: 550;
    }
    #textArea .properties{
        font-size: 18px;
        width: 80%;
    }
    #btn button{
        font-size: 18px;
        padding: 0.2em 0.3em;
        font-weight: 550;
    }
    .buttonGroup{
        width: 80%;
    }
    }
@media (max-width : 740px) {
    #header{
        height: auto;
        flex-direction: column;
        padding: 6px;
    }
    #header h1{
        font-size: 32px;
    }
    #header p{
        font-size: 16px;
        text-align: center;
        margin-top: 5px;
    }
    #textArea textarea{
        width: 80vw;
        font-size: 16px;
        font-weight: 550;
        margin-bottom: 9px;
    }
    #textArea .properties{
        font-size: 18px;
        width: 80%;
        gap: 4px 1.6em;
    }
    #footer{
        height: 20vh;
    }
    #footer h2{
        font-size: 26px;
        margin-top: 4px;
    }
    #footer pre{
        font-size: 18px;
        font-weight: 600;
    }
    #footer p{
        font-weight: 500;
        font-size: 16px;
    }
    }
    @media (max-width : 400px){
        #header h1{
            font-size: 28px;
        }
        #header p{
            font-size: 14px;
        }
        #textArea textarea{
            width: 94%;
        }
        #textArea .properties{
            font-size: 16px;
        }
        .buttonGroup{
            width: 100%;
        }
        #footer{
            height: 25vh;
        }
        #footer h2{
            font-size: 22px;
            text-align: center;
            margin-top: 4px;
        }
        #footer pre{
            font-size: 16px;
            font-weight: 550;
        }
        #footer p{
            font-weight: 500;
            font-size: 14px;
        }
    }